import React, { useState, useEffect, useCallback } from 'react'
import { withRouter } from 'react-router-dom'
import axios from 'axios'
import { Form, Button, Row, Col } from 'react-bootstrap';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';

const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});



function ResetPasswordBody() {

    let [alart, setAlart] = useState("");
    let [severity, setSeverity] = useState("");
    const axiosInstance = axios.create({
        baseURL: process.env.REACT_APP_BACKEND_URL,
    })


    let [form, setForm] = useState({})
    let [errors, setErrors] = useState({})
    let [open, setOpen] = useState(false);

    let setField = (field, value) => {
        setForm({
            ...form,
            [field]: value
        })
        if (!!errors[field]) setErrors({
            ...errors,
            [field]: null
        })
    }

    const findFormErrors = () => {
        const { newPassword1,newPassword2, oldPassword } = form
        const newErrors = {}
        // name errors
        if (!oldPassword || oldPassword === '') newErrors.oldPassword = 'cannot be blank!'
        if (!newPassword1 || newPassword1 === '') newErrors.newPassword1 = 'cannot be blank!'
        if (!newPassword2 || newPassword2 === '') newErrors.newPassword2 = 'cannot be blank!'
        if (newPassword2 !=newPassword1) newErrors.newPassword2 = 'not match!'
        // console.log(headersConfig);
        // console.log(newErrors)
        return newErrors
    }

   

    const onSubmit = (e) => {
        e.preventDefault()
        const newErrors = findFormErrors()
        // Conditional logic:
        // console.log(newErrors)
        if (Object.keys(newErrors).length > 0) {
            // We got errors!
            setErrors(newErrors)
        } else {
           

            axiosInstance.post('projonline/changepw',form
            , { headers: { token: "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken } })
                .then((res) => {
                    // console.log(res)
                    if(res.status == 202){
                        setSeverity("error")
                        setAlart("Check the current password")
                        setOpen(true)
                    }else if(res.status == 200){
                        setSeverity(res.data.variant)
                        setAlart("Password Changed Successfully")
                        setOpen(true)
                        setForm({
                            'oldPassword': '',
                            'newPassword1': '',
                            'newPassword2': '',
                        })
                    }
                   
                }).catch((error) => {
                    console.log(error)
                });
        }


    }

   



    return (
        <div>

            {/* Content Wrapper. Contains page content */}
            <div className="content-wrapper">
                {/* Content Header (Page header) */}
                <div className="content-header">
                    <section className="content-header">
                        <div className="container-fluid">
                            <div className="row mb-2">
                                <div className="col-sm-6">
                                    <h1>Reset Password</h1>
                                </div>
                            </div>
                        </div>{/* /.container-fluid */}
                    </section>
                </div>
                {/* /.content-header */}
                {/* Main content */}
                <section className="content">
                    <div className="container-fluid">
                        <Form onSubmit={onSubmit}>

                            <Row className="mb-3 ">
                                <Form.Group as={Col} >
                                    <Form.Label>Username</Form.Label>
                                    <Form.Control disabled value={JSON.parse(localStorage.getItem("user")).name} className="col-md-3" type="text" />
                                </Form.Group>
                            </Row>


                            <Row className="mb-3">
                                <Form.Group as={Col}>
                                    <Form.Label>Current Password</Form.Label>
                                    <Form.Control onChange={e => setField('oldPassword', e.target.value)} value={form.oldPassword} className="col-md-3" type="password" placeholder="Enter Current Password" isInvalid={!!errors.oldPassword} />
                                    <Form.Control.Feedback type='invalid'>
                                        {errors.oldPassword}
                                    </Form.Control.Feedback>
                                </Form.Group>
                            </Row>

                            <Row className="mb-3">
                                <Form.Group as={Col} >
                                    <Form.Label>New Password</Form.Label>
                                    <Form.Control onChange={e => setField('newPassword1', e.target.value)} value={form.newPassword1} className="col-md-3" type="password" placeholder="Enter New Password" isInvalid={!!errors.newPassword1} />
                                    <Form.Control.Feedback type='invalid'>
                                        {errors.newPassword1}
                                    </Form.Control.Feedback>
                                </Form.Group>
                            </Row>

                            <Row className="mb-3">
                                <Form.Group as={Col} >
                                    <Form.Label>Confirm New Password</Form.Label>
                                    <Form.Control onChange={e => setField('newPassword2', e.target.value)} value={form.newPassword2} className="col-md-3" type="password" placeholder="Confirm New Password" isInvalid={!!errors.newPassword2} />
                                    <Form.Control.Feedback type='invalid'>
                                        {errors.newPassword2}
                                    </Form.Control.Feedback>
                                </Form.Group>
                            </Row>
                            <Button variant="primary" type="submit">
                                Update Password
                            </Button>



                        </Form>


                    </div>{/* /.container-fluid */}
                </section>
                {/* /.content */}


            </div>

            <Snackbar open={open} autoHideDuration={6000} onClose={()=>setOpen(false)} anchorOrigin={{ vertical: "top", horizontal: "center" }}>
                <Alert onClose={()=>setOpen(false)} severity={severity} sx={{ width: '100%' }} >
                    {alart}
                </Alert>
            </Snackbar>


        </div>
    )
}

export default withRouter(ResetPasswordBody)